// –±–ª–∏–∂–∞–π—à–∏–π .search-wrapper –¥–ª—è –∫–ª–∞—Å—Å–∞ has-text
function getSearchWrapper() {
  return searchInput ? searchInput.closest('.search-wrapper') : null;
}

// –ø–æ–∫–∞–∑–∞—Ç—å / —Å–∫—Ä—ã—Ç—å –∫—Ä–µ—Å—Ç–∏–∫ —á–µ—Ä–µ–∑ –∫–ª–∞—Å—Å .has-text
function updateClearBtnState() {
  const wrapper = getSearchWrapper();
  if (!wrapper) return;

  if (searchInput.value.trim()) {
    wrapper.classList.add('has-text');
  } else {
    wrapper.classList.remove('has-text');
  }
}

// ==================== –ó–ê–ì–†–£–ó–ö–ê –ë–ï–ó KEYWORD ====================

// –≥—Ä—É–∑–∏–º –≤—Å–µ —É–ø—Ä–∞–∂–Ω–µ–Ω–∏—è –ø–æ —Ç–µ–∫—É—â–µ–π –ø–æ–¥–∫–∞—Ç–µ–≥–æ—Ä–∏–∏ (–±–µ–∑ keyword)
async function loadExercisesForCurrentCategory(page = 1) {
  const subcategory = selectedSubcategoryEl.textContent.trim().toLowerCase();
  if (!subcategory) {
    // –ø–æ–¥–∫–∞—Ç–µ–≥–æ—Ä–∏—è –Ω–µ –≤—ã–±—Ä–∞–Ω–∞ ‚Äî –ø—Ä–æ—Å—Ç–æ –Ω–∏—á–µ–≥–æ –Ω–µ –¥–µ–ª–∞–µ–º
    return;
  }

  const filterName = getCurrentFilter();
  const filterKey = mapFilterToKey(filterName);

  filtersGrid.innerHTML = '<p>Loading...</p>';

  try {
    const payload = {
      page,
      limit: 12,
    };
    payload[filterKey] = subcategory;

    const data = await api.getExercisesByFilters(payload);
    const items = Array.isArray(data.results) ? data.results : [];

    renderExercises(items);

    // pagination start
    if (paginationContainer) {
      const total = typeof data.total === 'number' ? data.total : items.length;
      const currentPage = data.page ?? page;
      const limit = data.limit ?? payload.limit ?? 12;
      const totalPages =
        typeof data.totalPages === 'number'
          ? data.totalPages
          : total
          ? Math.ceil(total / limit)
          : 0;

      renderPagination({
        container: paginationContainer,
        currentPage,
        totalPages,
        onPageChange: newPage => {
          loadExercisesForCurrentCategory(newPage);
        },
      });
    }
    // pagination end
						   
  } catch (error) {
    console.error(error);
    filtersGrid.innerHTML = '<p>Failed to load exercises</p>';
    iziToast.error({
      title: 'Error',
      message: 'Failed to load exercises',
      position: 'topRight',
    });
  }
}

// ==================== –ü–û–ò–°–ö –ü–û API –° KEYWORD ====================

async function runApiSearch(page = 1) {
  const keyword = searchInput.value.trim();
  const subcategory = selectedSubcategoryEl.textContent.trim().toLowerCase();
  const filterName = getCurrentFilter();
  const filterKey = mapFilterToKey(filterName);

  updateClearBtnState();

  if (!subcategory) {
    iziToast.info({
      title: 'Select category',
      message: 'Choose the category first',
      position: 'topRight',
    });
    return;
  }

  // –ü—É—Å—Ç–∞—è —Å—Ç—Ä–æ–∫–∞ ‚Üí –ø—Ä–æ—Å—Ç–æ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –≤—Å–µ —É–ø—Ä–∞–∂–Ω–µ–Ω–∏—è –∫–∞—Ç–µ–≥–æ—Ä–∏–∏
  if (!keyword) {
    await loadExercisesForCurrentCategory(1);
    return;
  }

  filtersGrid.innerHTML = '<p>Loading...</p>';

  try {
    const payload = {
      page,
      limit: 12,
      keyword,
    };
    payload[filterKey] = subcategory;

    const data = await api.getExercisesByFilters(payload);
    const items = Array.isArray(data.results) ? data.results : [];

    if (!items.length) {
      filtersGrid.innerHTML = '<p>There are no results for your query</p>';

      // pagination start
      if (paginationContainer) {
        renderPagination({
          container: paginationContainer,
          currentPage: 1,
          totalPages: 0,
          onPageChange: () => {},
        });
      }
      // pagination end

      return;
    }

    renderExercises(items);

    // pagination start
    if (paginationContainer) {
      const total = typeof data.total === 'number' ? data.total : items.length;
      const currentPage = data.page ?? page;
      const limit = data.limit ?? payload.limit ?? 12;
      const totalPages =
        typeof data.totalPages === 'number'
          ? data.totalPages
          : total
          ? Math.ceil(total / limit)
          : 1;

      renderPagination({
        container: paginationContainer,
        currentPage,
        totalPages,
        onPageChange: newPage => {
          runApiSearch(newPage);
        },
      });
    }
    // pagination end
  } catch (error) {
    console.error(error);
    iziToast.error({
      title: 'Error',
      message: 'Search failed',
      position: 'topRight',
    });
  }
}

// ==================== –û–ë–†–ê–ë–û–¢–ß–ò–ö–ò ====================

if (searchInput) {
  // input: –Ω—É–∂–µ–Ω –∏ –¥–ª—è Backspace, –∏ –¥–ª—è Ctrl+X –∏ —Ç.–ø.
  searchInput.addEventListener(
    'input',
    async e => {
      e.stopImmediatePropagation();
      updateClearBtnState();

      // –µ—Å–ª–∏ —Å—Ç—Ä–æ–∫–∞ —Å—Ç–∞–ª–∞ –ø—É—Å—Ç–æ–π ‚Üí —Å—Ä–∞–∑—É –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –≤—Å–µ —É–ø—Ä–∞–∂–Ω–µ–Ω–∏—è
      if (!searchInput.value.trim()) {
        await loadExercisesForCurrentCategory(1);
      }
    },
    true
  );

  // Enter ‚Üí –ø–æ–∏—Å–∫ –ø–æ API
  searchInput.addEventListener(
    'keydown',
    e => {
      if (e.key === 'Enter') {
        e.preventDefault();
        e.stopImmediatePropagation();
        runApiSearch(1);
      }
    },
    true
  );
}

if (searchBtn) {
  // –ö–ª–∏–∫ –ø–æ –ª—É–ø–µ ‚Üí –ø–æ–∏—Å–∫ –ø–æ API
  searchBtn.addEventListener(
    'click',
    e => {
      e.preventDefault();
      e.stopImmediatePropagation();
      runApiSearch(1);
    },
    true
  );
}

if (clearSearchBtn && searchInput) {
  // –ö–ª–∏–∫ –ø–æ –∫—Ä–µ—Å—Ç–∏–∫—É ‚Üí –æ—á–∏—Å—Ç–∫–∞ + –≤—Å–µ —É–ø—Ä–∞–∂–Ω–µ–Ω–∏—è
  clearSearchBtn.addEventListener(
    'click',
    async e => {
      e.preventDefault();
      e.stopImmediatePropagation();
      searchInput.value = '';
      updateClearBtnState();
      await loadExercisesForCurrentCategory(1);
      searchInput.focus();
    },
    true
  );
}
